// 修改打车模块
import WucTab from '@/components/wuc-tab/wuc-tabs.vue';
import {uniPopup} from "@/components/uni-popup/show_hw.vue"
import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';
export default {
	data() {
		return {
			//banner indicator-color
			indicatorColor: "#E8E8E8",
			indicatorActiveColor: "#000000",
			
			banner: [],
			news: [],
			scenicarea: [],
			culture: [],
			tickets: [],
			route: [],
			strategy: [],
			tour: [],
			TabCur: 0,
			tabList: [{
				name: '叫车'
			}, {
				name: '接送机'
			}],
			iconx: "true",
			moreicon: false,
			packup: false,    //一次 滑动判断点
			packups: false,  //二次 显示icon
			show: 0,
			iconbox: 0,
			// btuBottom:"",
			isIphoneX:false,
			
			//景区门票
			isautoplay: false,
			//banner 是否显示
			bannerShow: true,
			//打车弹框显示
			showMiddle : false,
			//显示地址列表
			showlist: 0,
			//位置
			whereyougo: '',
			whereyouare:'钱江·西溪新座',
			//打车地址列表
			addressList : [
				{
					name: '少林寺',
					address: '河南省郑州市登封市少林景区游客服务中心南侧',
				},
				{
					name: '嵩阳书院',
					address: '河南省郑州市近郊登封市太室路嵩阳南路',
				},
				{
					name: '中岳庙',
					address: '河南省登封市316省道（少林大道，市区以西约4公里）',
				},
				{
					name: '卢崖瀑布',
					address: '河南省登封市嵩山太室山悬练峰下',
				},
				{
					name: '太室山',
					address: '郑州市登封市中岳大街146号嵩山风景名胜区内',
				}
			]
		}

	},
	onLoad() {
		let _this = this;

		uni.getSystemInfo({
			success: res => {
				// console.log('手机信息res'+res.model)
				let modelmes = res.model;
				if (modelmes.search('iPhone X') != -1) {
					_this.isIphoneX = true;
					// _this.btuBottom="68rpx";
					// console.log(_this.isIphoneX,_this.btuBottom,123456)
				}
			},
		})
		uniRequest.post('/advertising/getadvertising', { //banner
		}).then(function(res) {
			
			_this.banner = res.data.data;

			console.log(res.data.data);
		}).catch(function(error) {
			console.log(error);
		});
		uniRequest.post('/articlecategory/getarticlelistbytitle', { //新闻轮播
			title: '嵩山资讯',
			limit: 4
		}).then(function(res) {
			_this.news = res.data.data;
			// console.log(res.data.data);
		}).catch(function(error) {
			console.log(error);
		});
		uniRequest.post('/articlecategory/getarticlecategorylistbytitle', { //嵩山景区
			title: '嵩山景区',
			limit: 4
		}).then(function(res) {
			_this.scenicarea = res.data.data;
			// console.log(res.data.data);
		}).catch(function(error) {
			console.log(error);
		});
		uniRequest.post('/tourguide/gettourguidelistforhome', {}).then(function(res) { //导游
			_this.tour = res.data.data;
			// console.log(res.data.data);
		}).catch(function(error) {
			console.log(error);
		});
		uniRequest.post('/articlecategory/getarticlecategorylistbytitle', { //景区文化
			title: '嵩山文化',
			limit: 5
		}).then(function(res) {
			_this.culture = res.data.data;
			// console.log(res.data.data);
		}).catch(function(error) {
			console.log(error);
		});
		ztRequest.get('/api/public/sell_product/newList', { //景区门票
			
		}).then(function(res) {
			console.log(res)
			// if(res.data.content.sellPrice.amount){}
			_this.tickets = res.data.content;
			// let array = [];
			// array.push(res.data.data[0]);
			// array.push(res.data.data[0]);
			// _this.tickets = array;
			//label 遍历 array
			// console.log(res.data.data);
		}).catch(function(error) {
			console.log(error);
		});
		
	},
	onShow(e) {
		console.log(e)
		this.getStrategy();
		this.getScenicTour();
	},
	// computed: {
	//           calUpx(num) {
	//               return uni.upx2px(num) + 'px';
	//           }
	//       },
	onPageScroll(e) {
		this.showMiddle = false;
		this.showlist = 0;
		// console.log(e.scrollTop)
		if (e.scrollTop > 94) {
			this.bannerShow = false;
			this.packup = true;
		} else {
			this.packup = false;
			this.bannerShow = true;
		}
		if (e.scrollTop > 255) {
			this.packups = true;
		} else {
			this.packups = false;
		}
		//console.log(this.packup)
	},

	methods: {
		//地址选择
		clickAddress(e){
			let url = e.target.dataset.t;
			this.whereyougo = url;
			this.closeShowMiddle(true);
		},
		//组件 显示列表地址
		getListShows(e){
			this.showlist = e;
			this.showMiddle = true;
		},
		//组件传值 控制
		gowhere(e){
			this.packups = e;
			this.showMiddle = true;
		},
		goAddress(){
			this.showMiddle = true;
			this.packups = false;
		},
		//关闭模板
		closeShowMiddle(e){
			if(e){
				this.packups = false;
			}else{
				this.packups = true;
				this.showMiddle = false;
			}
			this.showlist = 0;
		},
		//跳到票详情界面
		handleToDetail(val) {
			uni.navigateTo({
				url: '/pages/ticket/feizhuticket?id='+ val
			})
		},
		tobanner(e,type){
			if(type == 2){
				uni.navigateTo({
					url: '/pages/nothing/huerNothing'
				});
			}else{
				uni.navigateTo({
					url: e
				});
			}
			
		},
		nothing() {
			uni.navigateTo({
				url: '/pages/nothing/nothing'
			});
		},
		navticket() {
			uni.navigateTo({
				url: '/pages/ticket/ticketList'
			});
		},
		navhotel() {
			uni.navigateTo({
				url: '/pages/hotel/hotelList'
			});
		},
		navfood() {
			uni.navigateTo({
				url: '/pages/specialFood/specialFoodList'
			});
		},
		navshop() {
			uni.navigateTo({
				url: '/pages/shop/shopList'
			});
		},
		navtour() {
			uni.navigateTo({
				url: '/pages/tourGuide/tourGuide'
			});
		},
		navtours(id) {
			uni.navigateTo({
				url: `/pages/tourGuide/tourGuideParticulars?id=${id}`
			});
		},
		airplane() {
			uni.navigateToMiniProgram({
				appId: '60000138',
				path: '',
				success(res) {
					// 打开成功
				}
			})
		},
		tomine() {
			uni.reLaunch({
				url: '/pages/mine/mine',
			});
		},
		toticket() {
			uni.navigateTo({
				url: '/pages/ticket/ticketList',
			});
		},
		tocar() {
			uni.navigateToMiniProgram({
				appId: '2018122862752160',
				extraData: {},
				success(res) {
					// 打开成功
				}
			})
		},
		tofeizhu() {
			uni.navigateToMiniProgram({
				appId: '2018081461095002',
				extraData: {},
				success(res) {
					// 打开成功
				}
			})
		},
		tovar() {
			uni.navigateTo({
				url: '/pages/quanjin/quanjin'
			});
		},
		toculture(index, id) {
			console.log(index, id)
			uni.navigateTo({ //文化
				url: `/pages/culture/culture?index=${index}&id=${id}`

			});
		},
		toconsult(index) {
			uni.navigateTo({ //新闻
				url: '/pages/consult/consult'
			});
		},
		tonews(id) {
			uni.navigateTo({ //新闻详情
				url: '/pages/consult/consultParticulars?id=' + id

			});

		},
		tosenic(index, id) { //景区
			uni.navigateTo({
				url: `/pages/scenicArea/scenicArea?index=${index}&id=${id}`
			});
		},
		tofeizhuticket(id) { //门票详情页跳转
			uni.navigateTo({
				url: '/pages/ticket/feizhuticket?id=' + id
			});
		},
		tosenics() { //景区
			uni.navigateTo({
				url: `/pages/scenicArea/scenicArea?index=0`,

			});
		},
		tostrategy(index) { //攻略
			uni.navigateTo({
				url: '/pages/strategy/strategy'
			});
		},
		tostrategys(id) { //攻略
			uni.navigateTo({
				url: '/pages/strategy/strategyParticulars?id=' + id
			});
		},
		tojourney(index) { //行程
			uni.navigateTo({
				url: '/pages/journey/journey'
			});
		},
		tojourneys(id) { //行程
			uni.navigateTo({
				url: `/pages/journey/journeyParticulars?id=${id}`
			});
		},
		tabChange(index) {
			this.TabCur = index;
			// console.log(this.TabCur)
		},
		moreiconbtn() {
			this.moreicon = !this.moreicon
			// console.log(this.moreicon)
		},
		showcar() {
			this.show = true;
		},
		closecar(e) {
			this.show = false;
			console.log(e)
		},
		calUpx(num) {
		    return uni.upx2px(num) + 'px';
		},
		//景区攻略接口
		getStrategy(){
			let that = this;
			uniRequest.post('/articlecategory/getarticlelistbytitle', { //攻略
				title: '嵩山攻略',
				limit: 3
			}).then(function(res) {
				that.strategy = res.data.data;
				// console.log(res.data);
			}).catch(function(error) {
				console.log(error);
			});
		},
		//景区行程
		getScenicTour(){
			let _this = this;
			uniRequest.post('/articlecategory/getarticlelistbytitle', { //行程
				title: '嵩山行程',
				limit: 3
			}).then(function(res) {
				_this.route = res.data.data;
				// console.log(res);
			}).catch(function(error) {
				console.log(error);
			});
		}
	},
	components: { WucTab,uniPopup },
	
} 
